import axios from "axios";

const fixerUrl = "https://api.fixer.io/latest"

export function getRates(base) {
  return axios
    .get(fixerUrl, {
      params: {
        base
      }
    })
    .then(res => {
      const { data } = res,
        currency_list = [];

      currency_list.push({
        name: data.base,
        value: 1
      });

      for (let rate in data.rates) {
        currency_list.push({
          name: rate,
          value: data.rates[rate]
        });
      }

      return currency_list;
    });
}

export function convertRates(base, target) {
  return axios
    .get(fixerUrl, {
      params: {
        base: base,
        symbols: target
      }
    })
    .then(res => {
      const { data } = res,
        currency_list = [];

      return data.rates[target];
    });
}



